.container {
  width: 200px;
  height: 240px;
  padding-top: 0;
  box-shadow: 0 0 5px 2px rgb(0, 0, 0, 0.1);
  float: left;
  margin: 20px 0 0 20px;
}
.content {
  width: 100%;
  height: 40px;
  margin-top: -6px;
  padding: 0 5px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.text {
  font-weight: bold;
  /* width: 100px; */
  width: 120px;
  height: 90%;

  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
  z-index: 999;
}
